* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-left: 40px;
}

.list {
  list-style: none;
}

.example {
  font-family: Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  background-color: #fff;
  color: #424242;
  max-width: 960px;
  font-size: 32px;
  font-style: inherit;
  font-weight: 700;
  margin: 0;
  padding-top: 5px;
}

.title {
  font-family: Oxygen, sans-serif;
  font-size: 16px;
  font-weight: 800;
}
.upper {
  display: flex;
}
.upperTitle {
  padding-right: 10px;
}
.data {
  font-weight: 500;
  margin: 0;
}
.flag {
  margin: 0 10px;
}
